import ButtonBase from '@material-ui/core/ButtonBase';
import { makeStyles } from '@material-ui/styles';
import classNames from 'classnames';
import PropTypes from 'prop-types';
import React, { useEffect, useRef, useState } from 'react';

import styles from './Media.css';

const Media = ({ className, image, ...otherProps }) => {
  const classes = makeStyles(() => {
    return {
      loadImage: {},
      mediaBackground: {
        'html.no-webp &$loadImage': {
          backgroundImage: `url('${image.default}')`,
        },
        'html.webp &$loadImage ': {
          backgroundImage: `url('${image.webp}')`,
        },
      },
    };
  })();

  const [shouldLoadImage, setShouldLoadImage] = useState(false);
  const ref = useRef(null);

  useEffect(() => {
    if (`IntersectionObserver` in window) {
      const lazyImageObserver = new IntersectionObserver(
        (entries, observer) => {
          entries.forEach((entry) => {
            if (entry.isIntersecting) {
              setShouldLoadImage(true);
              observer.unobserve(entry.target);
            }
          });
        }
      );

      lazyImageObserver.observe(ref.current);
    } else {
      setShouldLoadImage(true);
    }
  }, []);

  console.log(image);
  console.log(`shouldLoadImage: ${shouldLoadImage}`);

  return (
    <ButtonBase
      {...otherProps}
      className={classNames(className, classes.mediaBackground, {
        [classes.loadImage]: shouldLoadImage,
      })}
      ref={ref}
    />
  );
};

Media.defaultProps = {
  className: null,
};

Media.propTypes = {
  className: PropTypes.string,
  image: PropTypes.shape({
    default: PropTypes.string.isRequired,
    webp: PropTypes.string.isRequired,
  }).isRequired,
};

export default Media;
